import React from 'react';
import '../css/Home.css';
import { Link } from 'react-router-dom';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import FeatureList from '../MenuPages/FeatureList';
import CartButton from '../components/Button';
import KitchenList from '../MenuPages/KitchenList';
import DealsList from '../MenuPages/DealsList';
import Slider from '../components/Slider';

const responsive = {
	desktop: {
		breakpoint: { max: 3000, min: 1024 },
		items: 3,
		slidesToSlide: 3 // optional, default to 1.
	},
	tablet: {
		breakpoint: { max: 1024, min: 464 },
		items: 2,
		slidesToSlide: 2 // optional, default to 1.
	},
	mobile: {
		breakpoint: { max: 464, min: 0 },
		items: 2,
		slidesToSlide: 1 // optional, default to 1.
	}
};

const responsiveFP = {
	desktop: {
		breakpoint: { max: 3000, min: 1024 },
		items: 3,
		slidesToSlide: 3 // optional, default to 1.
	},
	tablet: {
		breakpoint: { max: 1024, min: 464 },
		items: 2,
		slidesToSlide: 2 // optional, default to 1.
	},
	mobile: {
		breakpoint: { max: 464, min: 0 },
		items: 1,
		slidesToSlide: 1 // optional, default to 1.
	}
};
function HomeKitchen() {

	const KitchenCards = KitchenList.map(kitchen =>
		<Slider key={kitchen.ID.toString()} linkprop={kitchen.Link} imgsrc={kitchen.ImgSrc} cardtitle={kitchen.CardTitle} cardtext={kitchen.Description} />
	)
	const DealsCards = DealsList.map(Deal =>
		<Slider key={Deal.ID.toString()} linkprop={Deal.Link} imgsrc={Deal.ImgSrc} cardtitle={Deal.CardTitle} cardtext={Deal.Description} />
	)

	return (
		<div className="home col-lg-12 flex">
			<div className="col-lg-12 ">
				<div className="col-lg-12 ">
					<div className="col-lg-12 caraousal-header"><h4>Kitchens</h4></div>
				</div>
				<Carousel autoPlay={false} arrows={true} showDots={false} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsive} >
					{KitchenCards}
				</Carousel>
			</div>
			<div className="col-lg-12 ">
				<div className="col-lg-12 ">
					<div className="col-lg-12 caraousal-header"><h4>Deals Zone</h4></div>
				</div>
				<Carousel autoPlay={false} arrows={true} showDots={false} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsive} >
					{DealsCards}
				</Carousel>
			</div>
			<div className="col-lg-12 product " style={{ display: 'flex' }}>
				<div className="col-lg-12 col-xs-12 feature-header"><h4>Featured Products</h4></div>
			</div>
			<Carousel autoPlay={false} arrows={true} showDots={false} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsiveFP} >
				{FeatureList.map((kitchen) => (
					<div className="card">
						<Link to={kitchen.Link}><img className="card-img-top feature-img" src={kitchen.ImgSrc} alt="Card image cap" /></Link>
						<div className="card-body feature-body">
							<h5 className="card-title">{kitchen.CardTitle}</h5><CartButton />
							<p className="card-text">{kitchen.Description}</p>
						</div>
					</div>
				))}
			</Carousel>

		</div>
	);
};

export default HomeKitchen;

