import React from 'react'
import { HiOutlineCurrencyRupee } from "react-icons/hi";

function SmallSlider(props) {
    return (
        <div className="discount-card">
            <div className="discount-body">
                <h5 className="card-title">{props.discount}</h5>
                <p className="card-text">{props.code}</p><hr className="kitchen-hr" />
                <p className="card-condition">Above <HiOutlineCurrencyRupee className="offer" />{props.amount}</p>
            </div>
        </div>
    )
}

export default SmallSlider