import React from 'react'

import { MdStarBorder } from "react-icons/md";
import { HiOutlineCurrencyRupee } from "react-icons/hi";

function MenuHeader(props) {
    return (
        <div className="col-lg-12">
            <h4><b>{props.kitchenName}</b></h4>
            <h6>{props.description}</h6>
            <h6>{props.city}</h6>
            <div className="col-lg-12 col-xs-12 col-md-12 col-sm-12 kitchen-header">
                <div className="col-lg-4 col-xs-4 col-md-4 col-sm-4" style={{ 'textAlign': 'center' }}><b>{props.rating}<MdStarBorder className="star" /></b></div>
                <div className="col-lg-4 col-xs-2 col-md-4 col-sm-2" style={{ 'textAlign': 'center' }}><b>{props.timetodel}</b><br />Delivery</div>
                <div className="col-lg-4 col-xs-6 col-md-4 col-sm-6" style={{ 'textAlign': 'end' }}><b><HiOutlineCurrencyRupee className="star" />{props.amount}</b><br /><span>Cost for 2</span></div>
            </div>
        </div>
    )
}

export default MenuHeader