import React from 'react';
import '../css/KitchenHome.css';
import "bootstrap/dist/css/bootstrap.min.css";
import { Link } from 'react-router-dom';
import MenuHeader from '../components/MenuHeader'
import { BiArrowBack, BiDownArrowCircle } from "react-icons/bi";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Kitchen21Menu from '../MenuPages/Kitchen21menu';
import SmallSlider from '../components/SmallSlider';
import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button'
import Card from 'react-bootstrap/Card';
import { BiRupee } from "react-icons/bi";

const Type = [
	{ id: 1, label: "Rice" },
	{ id: 2, label: "Roti" },
	{ id: 3, label: "Curry" },
	{ id: 4, label: "Pizza" },
	{ id: 5, label: "Burger" },
	{ id: 6, label: "Sandwich" },
]
const promotion = [
	{
		'ID': 0,
		'Discount': '50% OFF UPTO 100',
		'Code': 'USE KITCHEN100',
		'Amount': '129'
	},
	{
		'ID': 1,
		'Discount': 'FREE DELIVERY',
		'Code': 'USE FREEDEL',
		'Amount': '129'
	},
	{
		'ID': 2,
		'Discount': '20% OFF UPTO 100',
		'Code': 'USE RUPAY100',
		'Amount': '129'
	}
]
const responsive = {
	desktop: {
		breakpoint: { max: 3000, min: 1024 },
		items: 2,
		slidesToSlide: 3
	},
	tablet: {
		breakpoint: { max: 1024, min: 464 },
		items: 2,
		slidesToSlide: 2
	},
	mobile: {
		breakpoint: { max: 400, min: 0 },
		items: 1,
		slidesToSlide: 1
	}
};

class KitchenHome extends React.Component {
	constructor() {
		super()
		this.state = {
			activeKey: true
		}
		this.rotateArrow = this.rotateArrow.bind(this);
	}
	rotateArrow() {
		this.setState(prevState => {
			return {
				activeKey: !prevState.activeKey
			}
		})
	}
	render() {
		const DiscountCards = promotion.map(promo =>
			<SmallSlider key={promo.ID} discount={promo.Discount} code={promo.Code} amount={promo.Amount} />
		)

		return (
			<div className="kitchen-21">
				<Link to="/egapp/"><BiArrowBack /></Link>
				<MenuHeader kitchenName="Kitchen21" description="Indian, Italian" city="Brahmapur" rating="5" timetodel="30mins" amount="200" />
				<div className="col-lg-12 carousal" >
					<Carousel autoPlay={false} arrows={false} showDots={true} swipeable={true} infinite={true} autoPlaySpeed={2500} responsive={responsive} >
						{DiscountCards}
					</Carousel>
				</div >
				<div className="col-lg-12 kitchen-menu">
					{Type.map(item => (
						<Accordion key={item.id} defaultActiveKey={item.id} onClick={this.rotateArrow}>
							<Card className="acc-card" >
								<Accordion.Toggle as={Card.Header} eventKey={item.id}>
									<p className="acc-header">{item.label}
										<BiDownArrowCircle className={this.state.activeKey ? 'arrow-toggle' : null} style={{ 'float': 'right' }} />
									</p>
								</Accordion.Toggle>
								<Accordion.Collapse eventKey={item.id}>
									<Card.Body>
										{Kitchen21Menu.map((menu) => (
											item.label == menu.Type ?
												<div className="col-lg-12 col-xs-12 col-md-12 col-sm-12 item-name" >
													<div className="col-lg-6 col-xs-6 col-md-6 col-sm-6 item-title">
														<h5 className="menu-title">{menu.Name}</h5>
														<p className="menu-price"><BiRupee className="rupee-style" />{menu.Price}</p>
													</div>
													<div className="col=lg-6 col-xs-6 col-md-6 col-sm-6 item-img">
														<img className="kitchen-img" src={menu.ImgSrc} alt={menu.Name} /><br />
														<Button className="add-btn" variant="warning" onClick={() => console.log("added")}></Button>

													</div>
												</div> : <span style={{ 'display': 'none' }}></span>

										))}
									</Card.Body>
								</Accordion.Collapse>
							</Card>
						</Accordion>
					))}

					<div className="col-lg-6 kitchen-image"></div>
				</div>
			</div>
		);
	}
};
{/* <Button className="arrow-toggle" variant="warning" onClick={setOpen}><BiDownArrowCircle /></Button> {activeKey == item.id ? 'arrow-toggle' : null}*/ }
export default KitchenHome;
