import React from 'react';
import { FormControl, Button } from 'react-bootstrap';
import { useLocation } from "react-router-dom";
import { FaPizzaSlice, FaUtensils, FaSearch, FaMapMarkerAlt } from 'react-icons/fa';
import './Navbar.css';
import { CgShoppingCart } from 'react-icons/cg';
import 'react-bootstrap'

const Navigation = () => {
  //assigning location variable
  const location = useLocation();

  //destructuring pathname from location
  const { pathname } = location;

  //Javascript split method to get the name of the path in array
  const splitLocation = pathname.split("/egapp/");

  return (
    <header >
      <div className="col-lg-12 nav-header">
        <div className="col-lg-3 col-md-3 col-xs-12 greet-header-text">
          <h5>Oh MY Good food ! </h5>
        </div>
        <div className="search-btn col-lg-6 col-md-6 col-xs-12 col-sm-10">
          <Button className=""><FaSearch /></Button>
          <input type="text" className="form-control" placeholder="Search your OMGF!" />
        </div>
        <div className="col-lg-3 col-md-3 col-xs-12 col-sm-12 location">
          <div className="col-xs-6" style={{ width: '80%' }}>
            <FaMapMarkerAlt style={{ marginTop: '5px', marginBottom: '2%' }} /><p style={{ textAlign: 'left', fontWeight: 'bold' }}>Your location..</p>
          </div>
          <div className="col-xs-6">
            <CgShoppingCart style={{ marginTop: '5px', marginBottom: '7%' }} /><p style={{ textAlign: 'center', fontWeight: 'bold' }}>Cart</p>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Navigation;